
Template.__checkName("pages_menu");
Template["pages_menu"] = new Template("Template.pages_menu", (function() {
  var view = this;
  return Blaze.If(function() {
    return Spacebars.call(view.lookup("hasPages"));
  }, function() {
    return [ "\n    ", HTML.DIV({
      "class": function() {
        return [ "pages-menu ", Spacebars.mustache(view.lookup("moduleClass")) ];
      }
    }, "\n      ", Blaze.Each(function() {
      return Spacebars.call(view.lookup("pages"));
    }, function() {
      return [ "\n        ", HTML.DIV({
        "class": "pages-menu-item"
      }, "\n          ", HTML.A({
        href: function() {
          return Spacebars.mustache(view.lookup("pathFor"), "page", Spacebars.kw({
            slug: Spacebars.dot(view.lookup("."), "slug")
          }));
        }
      }, Blaze.View("lookup:title", function() {
        return Spacebars.mustache(view.lookup("title"));
      })), "\n        "), "\n      " ];
    }), "\n    "), "\n  " ];
  });
}));
