
Template.__checkName("pages");
Template["pages"] = new Template("Template.pages", (function() {
  var view = this;
  return [ HTML.DIV({
    "class": "form-well add-page"
  }, "\n    ", HTML.Raw("<h3>Add new page:</h3>"), "\n    ", Blaze._TemplateWith(function() {
    return {
      collection: Spacebars.call("Pages"),
      id: Spacebars.call("insertPageForm"),
      type: Spacebars.call("insert"),
      "label-class": Spacebars.call("control-label"),
      "input-col-class": Spacebars.call("controls"),
      template: Spacebars.call("bootstrap-horizontal")
    };
  }, function() {
    return Spacebars.include(view.lookupTemplate("quickForm"));
  }), "\n  "), "\n  ", Blaze.Each(function() {
    return Spacebars.call(view.lookup("pages"));
  }, function() {
    return [ "\n    ", Spacebars.include(view.lookupTemplate("page_item")), "\n  " ];
  }) ];
}));
