
Template.__checkName("page");
Template["page"] = new Template("Template.page", (function() {
  var view = this;
  return Spacebars.With(function() {
    return Spacebars.call(view.lookup("page"));
  }, function() {
    return [ "\n    ", HTML.DIV({
      "class": "page-container"
    }, "\n      ", HTML.H2({
      "class": "page-title"
    }, Blaze.View("lookup:title", function() {
      return Spacebars.mustache(view.lookup("title"));
    })), "\n      ", HTML.DIV({
      "class": "page-body markdown",
      "aria-live": "polite"
    }, "\n        ", Spacebars.include(view.lookupTemplate("markdown"), function() {
      return Blaze.View("lookup:content", function() {
        return Spacebars.mustache(view.lookup("content"));
      });
    }), "\n      "), "\n    "), "\n  " ];
  });
}));
