
Template.__checkName("page_item");
Template["page_item"] = new Template("Template.page_item", (function() {
  var view = this;
  return HTML.DIV({
    "class": "form-module"
  }, "\n    ", Blaze._TemplateWith(function() {
    return {
      collection: Spacebars.call("Pages"),
      id: Spacebars.call(view.lookup("formId")),
      type: Spacebars.call("update"),
      doc: Spacebars.call(view.lookup(".")),
      "label-class": Spacebars.call("control-label"),
      "input-col-class": Spacebars.call("controls"),
      template: Spacebars.call("bootstrap-horizontal")
    };
  }, function() {
    return Spacebars.include(view.lookupTemplate("quickForm"));
  }), HTML.Raw('\n    <a href="#" class="delete-link">Delete</a>\n  '));
}));
